<template>
  <div id="app">
    <div id="top_of_page"></div>
    <div>
      <Header :root="this" />
      <div class="el_contenido_de_pagina_central">
        <div class="" style="text-align: left">
          <div v-if="!current_page" v-bind:key="'carga-de-pagina'" class="un_espaciado un_texto_pequenio">Cargando la página... Por favor, aguarde</div>
          <div v-else style="display: block">
            <div class="">
              <Breadcrumb :root="this" ref="MainAppBreadcrumb" />
            </div>
            <div class="" v-if="is_ahora_active">
              <PanelDeAhora :root="this" />
            </div>
            <div class="">
              <component
                :root="this"
                :is="current_page"
                v-bind="{
                  key: $utils.getRandomString(20),
                  pageProperties: current_page_properties,
                }"
              />
            </div>
          </div>
        </div>
      </div>
      <Footer :root="this" />
    </div>
    <Intersitial v-if="is_loading" :root="this" :on-click="() => (is_loading = false)" />
  </div>
</template>

<script>
import "./assets/styles/infrastructure.css";
import "./assets/styles/theme.css";
import "./assets/styles/framework.css";
import "./assets/styles/overridden.css";

export default {
  name: "app",
  components: {},
  data() {
    return {
      current_page: "PaginaHome",
      current_page_properties: undefined,
      is_navigation_menu_active: false,
      is_ahora_active: false,
      is_loading: true,
      user_data: {},
    };
  },
  methods: {
    goBack() {
      [...document.querySelectorAll(".Component-Breadcrumb div span a")].pop().click();
    },
    goToTopOfPage() {
      document.body.scrollTop = 0;
      document.body.scrollIntoView(true);
    },
    goToTarea(tarea) {
      this.goToPage("PaginaAgendaEditarTarea", {
        value: tarea,
      });
    },
    goToPage(page, pageProperties = undefined) {
      console.log("Opening page: ", page, JSON.stringify(pageProperties || null, null, 4));
      this.setCurrentPage(undefined, {});
      this.setCurrentPage(page, pageProperties);
      this.goToTopOfPage();
      this.hideMenu();
    },
    setCurrentPage(page, pageProperties = undefined) {
      this.current_page_properties = pageProperties;
      this.current_page = page;
      if (!("MainAppBreadcrumb" in this.$refs)) {
        return console.error("Breadcrumb not loaded...");
      }
      this.$refs.MainAppBreadcrumb.refreshPoints();
    },
    toggleMenu() {
      this.is_navigation_menu_active = !this.is_navigation_menu_active;
    },
    toggleAhora() {
      this.is_ahora_active = !this.is_ahora_active;
    },
    hideMenu() {
      this.is_navigation_menu_active = false;
    },
    showMenu() {
      this.is_navigation_menu_active = true;
    },
    initializeUserData() {
      const settings = this.$db.getSettings();
      this.user_data = Object.assign({}, settings, { username: "Pepito Yons Smidsonian" });
    },
  },
  mounted() {
    window.app = this;
    this.setCurrentPage("PaginaHome");
    this.initializeUserData();
    setTimeout(() => (this.is_loading = false), 10 * 1000);
  },
};
</script>

