<template>
  <div class="Component-PanelDeAhora">
    <div class="el_contenedor_de_los_cronometros">
      <h3 class="texto_importante_1">Ahora:</h3>
      <div class="un_espaciado" v-if="(!Array.isArray(internal_tareas_de_tiempo)) || (internal_tareas_de_tiempo.length === 0)">
        <div class="un_texto_menor">No hay tareas para ahora mismo.</div>
      </div>
      <div class="un_espaciado_horizontal" v-else>
        
          <ul class="una_lista_de_tareas un_espaciado_horizontal">
            <li class="" :class="{ pendiente: !tarea['está terminada'] }" :style="tareaIndex !== 0 ? 'margin-top: 5px' : ''" v-for="(tarea, tareaIndex) in internal_tareas_de_tiempo" v-bind:key="'tareas-de-ahora-' + tareaIndex">
              <div class="un_texto_pequenio">
                <div class="un_espaciado una_cabecera_de_tarea">
                  <div class="" style="font-weight: bold; text-decoration: underline">
                    <span class="">{{ tarea["está terminada"] ? "✔" : "✖" }} </span>
                    <span class="">{{ $utils.abbreviateTareaId(tarea.nombre) }}</span>
                  </div>
                  <div class="un_espaciado_arriba" style="color: #ccc; font-size: 10px">Para: {{ $utils.getFullPoliteStringFromStringDate(tarea["fecha de inicio"]) }}</div>

                  <!--span class="" v-if="timeIntervals === 'hora'"> {{ tarea["fecha de inicio"].split(" ")[1] }} </span>
              <span class="" v-else-if="timeIntervals === 'día'"> {{ $utils.getPoliteStringFromStringDate( tarea["fecha de inicio"] ) }} </span>
              <span class="" v-else-if="timeIntervals === 'mes'"> {{ $utils.getPoliteStringFromStringDate( tarea["fecha de inicio"] ) }} </span>
              <span class="" v-else-if="timeIntervals === 'anio'"> {{ $utils.getPoliteStringFromStringDate( tarea["fecha de inicio"] ) }} </span>
              <span class=""> {{ tarea["está terminada"] ? "" : "" /*"[POR HACER]"*/ }} » </span>
              <span style="font-weight: bold; text-decoration: underline">{{ $utils.replaceTareaId(tarea.nombre || "") }} </span-->
                </div>
                <div class="un_espaciado un_cuerpo_de_tarea" v-if="tarea['descripción'] || tarea.detalles">
                  <div style="font-weight: bold; color: #e0e0e0; text-shadow: 0 0 1px black">{{ tarea["descripción"] }}</div>
                  <div style="font-weight: bold; color: #c8c8c8; text-shadow: 0 0 1px black">{{ tarea.detalles }}</div>
                </div>
                <div class="como_una_tabla ancho_maximo un_texto_muy_pequenio un_pie_de_tarea un_espaciado" style="display: block; text-align: right">
                  <span class="como_una_celda ancho_maximo un_espaciado_derecha" v-if="tarea['está terminada']" style="white-space: nowrap">
                    <button class="un_control un_boton_de_la_tarea" v-on:click="() => $db.undoTarea(tarea.id).then(() => refreshTareas())">Deshacer #{{ tarea.id }}</button>
                  </span>
                  <span class="como_una_celda ancho_maximo un_espaciado_derecha" v-else>
                    <button class="un_control un_boton_de_la_tarea" v-on:click="() => $db.doTarea(tarea.id).then(() => refreshTareas())">Hacer #{{ tarea.id }}</button>
                  </span>
                  <span class="como_una_celda un_espaciado_derecha">
                    <button class="un_control un_boton_de_la_tarea" v-on:click="() => root.goToTarea(tarea)">Editar</button>
                  </span>
                  <span class="como_una_celda" style="white-space: nowrap">
                    <button class="un_control un_boton_de_la_tarea" v-on:click="() => $db.deleteTarea(tarea.id).then(() => refreshTareas())">Eliminar</button>
                  </span>
                </div>
              </div>
            </li>
          </ul>
        
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "PanelDeAhora",
  props: {
    root: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      internal_tareas_de_tiempo: undefined,
    };
  },
  methods: {
    refreshTareas() {
      const dateAhoraStr = this.$utils.getStringFromDate(new Date(), "hora");
      this.$db
        .select("tarea", {
          filter: (tarea) => {
            return tarea["fecha de inicio"].startsWith(dateAhoraStr);
          },
        })
        .then((tareasHoy) => {
          this.internal_tareas_de_tiempo = tareasHoy;
          return tareasHoy;
        });
    },
  },
  watch: {},
  computed: {},
  beforeCreate() {},
  beforeMount() {},
  beforeUpdate() {},
  beforeDestroy() {},
  created() {},
  mounted() {
    this.refreshTareas();
  },
  updated() {},
  destroyed() {},
};
</script>
