<template>
  <div class="Component-IntroductorioPorHora">
    <div class="un_espaciado" v-if="!internal_tiempo">Cargando...</div>
    <div class="un_espaciado_sin_arriba" v-else>
      <div class="" :class="{ pendiente: $utils.hasTareasPendientes(this) }">
        <div class="" style="margin-top: 0px">
          <div class="como_una_tabla">
            <span class="como_una_celda">
              <button class="un_control una_anchura_de_20_px" v-on:click="() => goToPreviousMonth()">«</button>
            </span>
            <span class="como_una_celda ancho_maximo texto_centrado rompible_por_espacios">{{
              $utils
                .getPoliteMonthStringFromDate(internal_tiempo)
                .replace(/^[^,]+, /g, "")
                .trim()
            }}</span>
            <span class="como_una_celda">
              <button class="un_control una_anchura_de_20_px" v-on:click="() => goToNextMonth()">»</button>
            </span>
          </div>
        </div>
      </div>
      <div class="texto_centrado el_mensaje_de_no_asignados" v-if="!internal_tareas_de_tiempo || !internal_tareas_de_tiempo.length">
        <p>No hay tareas asignadas a este mes.</p>
      </div>
      <div class="" v-else>
        <div class="el_panel_de_configuraciones_del_introductorio">
          <PanelAnaliticoIntroductorio :root="root" :introductorio="this" time-intervals="mes" />
        </div>
      </div>
      <div class="un_texto_pequenio texto_centrado">
        <button class="un_control" v-on:click="() => goToAddTarea()">Crear nueva tarea</button>
      </div>
    </div>
  </div>
</template>
<script>
import IntroductorioInterface from "./interfaces/IntroductorioInterface.js";

export default {
  name: "IntroductorioPorMes",
  props: {
    root: {
      type: Object,
      required: true,
    },
  },
  data() {
    return { ...IntroductorioInterface.data() };
  },
  methods: {
    ...IntroductorioInterface.methods,
    refreshInternalTiempoData(v = this.internal_tiempo) {
      if (!(v instanceof Date)) {
        this.internal_tareas_de_tiempo = undefined;
        return;
      }
      const startDateStr = `${this.$utils.padStart(v.getFullYear(), 4, "0")}-${this.$utils.padStart(v.getMonth() + 1, 2, "0")}`;
      this.$db
        .select("tarea", {
          filter: (item) => {
            return item["fecha de inicio"].startsWith(startDateStr);
          },
        })
        .then((dataP) => {
          const data = dataP.sort((a, b) => {
            return b["fecha de inicio"] < a["fecha de inicio"] ? 1 : a["fecha de inicio"] < b["fecha de inicio"] ? -1 : b.nombre < a.nombre ? 1 : -1;
          });
          this.internal_tareas_de_tiempo_fallidas = data.filter((v2) => {
            return !v2["está terminada"];
          });
          this.internal_tareas_de_tiempo_completadas = data.filter((v2) => {
            return v2["está terminada"];
          });
          this.internal_tareas_de_tiempo_objetivos_totales = data.length;
          this.internal_tareas_de_tiempo_objetivos_sumados = data
            .filter((v2) => {
              return v2["está terminada"];
            })
            .reduce(this.$utils.reduceObjetivosFromTarea.bind(this), []);
          this.internal_tareas_de_tiempo_objetivos_pendientes = data
            .filter((v2) => {
              return !v2["está terminada"];
            })
            .reduce(this.$utils.reduceObjetivosFromTarea.bind(this), []);
          this.internal_tareas_de_tiempo_objetivos_totales = this.internal_tareas_de_tiempo_objetivos_sumados.length + this.internal_tareas_de_tiempo_objetivos_pendientes.length;
          this.internal_tareas_de_tiempo = data;
          return data;
        })
        .catch((error) => {
          this.internal_error = error;
          throw error;
        });
    },
  },
  watch: { ...IntroductorioInterface.watch },
  ...IntroductorioInterface.directProperties,
};
</script>
